<template>
  <div class="pushmore-index index-er">
    <div class="head">
      <img :src="userInfoData.headImg" />
      <div class="head-text">
        <p>
          <label></label>
          <span>{{userInfoData.nickName}}</span>
        </p>
        <p>
          <label>ID</label>
          <span>{{userInfoData.id}}</span>
        </p>
        <p>
          <label></label>
          <span>{{userInfoData.recommender}}</span>
        </p>
      </div>
    </div>
    <div><p class="recommend-text"></p></div>
    <div class="switch-but" v-if="otherHad == true" @click="qwPushmore"></div>
    <div class="progress-bar" v-for="(item,index) in activityInfo" :key="index">
      <p class="rank rank_tb">{{item.maxInvitees}}<span class="complete" v-if="item.isComplete==1"></span></p>
      <ul>
        <li>
          <p class="award-progress">
            <span class="progress">
              <i></i> <i></i>
            </span>
            <span class="horizontal-line" style="height:6px;">
              <i :class="item.invitees / item.everyAwardCount == 0.25 || item.invitees/item.everyAwardCount > 0.25?'line-ibgk':''" style="height:2px;"></i>
            </span>
          </p>
        </li>
        <li>
          <p class="bar-p">
            <span :class="item.invitees / item.everyAwardCount == 0.25 || item.invitees / item.everyAwardCount < 0.5 && item.invitees / item.everyAwardCount > 0.25?'bar-span bgk-bar':'bar-span'">
              <i :class="item.invitees / item.everyAwardCount == 0.25 || item.invitees / item.everyAwardCount > 0.25?'iconfont icon-chenggong bar-spancolor':'iconfont icon-chenggong'"></i>
            </span>
            <span class="horizontal-line">
              <i :class="item.invitees / item.everyAwardCount == 0.5 || item.invitees / item.everyAwardCount > 0.5?'line-ibgk':''"></i>
            </span>
          </p>
          <p :class="item.invitees / item.everyAwardCount == 0.25 || item.invitees / item.everyAwardCount > 0.25?'zs-text bar-spancolor':'zs-text'">1</p>
        </li>
        <li>
          <p class="bar-p">
            <span :class="item.invitees / item.everyAwardCount == 0.5 || item.invitees / item.everyAwardCount < 0.75 && item.invitees / item.everyAwardCount > 0.5?'bar-span bgk-bar':'bar-span'">
              <i :class="item.invitees / item.everyAwardCount == 0.5 || item.invitees / item.everyAwardCount > 0.5?'iconfont icon-chenggong bar-spancolor':'iconfont icon-chenggong'"></i>
            </span>
            <span class="horizontal-line">
              <i :class="item.invitees / item.everyAwardCount >= 0.75 || item.invitees / item.everyAwardCount > 0.75 ?'line-ibgk':''"></i>
            </span>
          </p>
          <p :class="item.invitees / item.everyAwardCount >= 0.5 || item.invitees / item.everyAwardCount > 0.5?'zs-text bar-spancolor':'zs-text'">2</p>
        </li>
        <li>
          <p class="bar-p">
            <span :class="item.invitees / item.everyAwardCount == 0.75 || item.invitees / item.everyAwardCount < 1 && item.invitees / item.everyAwardCount > 0.75?'bar-span bgk-bar':'bar-span'">
              <i :class="item.invitees / item.everyAwardCount == 0.75 || item.invitees / item.everyAwardCount >0.75?'iconfont icon-chenggong bar-spancolor':'iconfont icon-chenggong'"></i>
            </span>
            <span class="horizontal-line">
              <i :class="item.invitees / item.everyAwardCount >= 1 || item.invitees / item.everyAwardCount > 1?'line-ibgk':''"></i>
            </span>
          </p>
          <p :class="item.invitees / item.everyAwardCount >= 0.75 || item.invitees / item.everyAwardCount > 0.75?'zs-text bar-spancolor':'zs-text'">3</p>
        </li>
        <li>
          <p class="bar-p">
            <span :class="item.invitees / item.everyAwardCount == 1 || item.invitees / item.everyAwardCount > 1?'bar-span bgk-bar':'bar-span'">
              <i :class="item.invitees / item.everyAwardCount == 1 || item.invitees / item.everyAwardCount > 1?'iconfont icon-chenggong bar-spancolor':'iconfont icon-chenggong'"></i>
              </span>
          </p>
          <p :class="item.invitees / item.everyAwardCount >= 1 || item.invitees / item.everyAwardCount > 1?'zs-text bar-spancolor':'zs-text'">4</p>
        </li>
        <li class="jindu-li">
          <p class="toTxchange" :style="item.prize <= 0 ?'background-color: #999;':'background-color: #ff6600;'" @click="exchangeRules(item.prize,item.activityId)"></p>
          <p class="numberGifts"><span>{{item.prize}}</span></p>
        </li>
      </ul>
      <p class="rank">~<span>{{item.brandName}}</span>~</p>
    </div>
    
    <van-overlay :show="show2">
      <div class="exchange-rules">
        <h3 class="title"></h3>
        <p class="exchange-number">
          
          <span>{{prize}}</span>
        </p>
        <div class="this-exchange">
          <span class="this-text"></span>
          <div class="stepper">
            <div class="strip">
              <van-stepper
                class="step"
                min="0"
                :value="stepValue"
                async-change
                @change="onChangeStep"
                :disable-plus="prohibitStep"
                disable-input
                @plus="stripPlus"
                @minus="stripMinus"
              />
              <div class="tuijian"><p class="tuijian-text"></p><p class="tiaoText"></p></div>
            </div>
            <div class="strip marg-top">
              <van-stepper
                class="step"
                min="0"
                :value="shareValue"
                async-change
                @change="onChangeShare"
                :disable-plus="prohibitShare"
                disable-input
                @plus="sharePlus"
                @minus="shareMinus"
              />
              <span class="bao"></span>
            </div>
          </div>
        </div>
        <div class="tips">
          <span class="tips-text"></span>
          <div class="explain">
            <p>1</p>
            <p>21</p>
          </div>
        </div>
        <div class="but">
          <button class="butts bgkhui" @click="cancel"></button>
          <button class="butts bgkblue" @click="determine"></button>
        </div>
      </div>
    </van-overlay>

    <div class="menu-list">
      <div class="list" @click="myTeam">
        <i class="iconfont icon-tuandui icon"></i>
        <span class="middle-text"></span>
        <i class="iconfont icon-gengduo icon-more"></i>
      </div>
      <div class="list" @click="myQrCode">
        <i class="iconfont icon-two-dimensional-code icon icon-middle"></i>
        <i class="iconfont icon-AppIcon-copy card-circle"></i>
        <i class="iconfont icon-shouzhixuanzhong-copy card-finger"></i>
        <span class="middle-text">
          <span class="card"></span>
          <span v-show="remainingTime" class="remaining-time">(...)<i class="iconfont icon-dian-red remaining-icon"></i></span>
        </span>
        <i class="iconfont icon-gengduo icon-more"></i>
      </div>
      <div class="list none-border" @click="orderManagement">
        <i class="iconfont icon-dingdan icon icon-right"></i>
        <span class="middle-text"></span>
        <i class="iconfont icon-gengduo icon-more"></i>
      </div>
      <div class="list none-border" @click="myReward">
        <i class="iconfont icon-jiangli icon icon-right"></i>
        <span class="middle-text"></span>
        <i class="iconfont icon-gengduo icon-more"></i>
      </div>
    </div>

    <div class="reminder">
      <i class="iconfont icon-jinggao reminder-icon"></i>
      <span></span>
    </div>

   <div class="activity-rules-desc" id="rules">
      <h3></h3>
      <p>1</p>
      <p>2</p>
      <p>3</p>
      <p>440</p>
      <p>54008792099</p>
    </div>
    <van-overlay :show="show3">
      <div class="exchange-rules">
        <i class="iconfont icon-guanbi guanbi" @click="guanbiBut"></i>
        <h3 class="title"></h3>
        <p class="exchange-number">
          
          <span>{{stepValue}}+{{shareValue}}</span>
        </p>
        <p class="exchange-number"></p>
        <van-field v-model="name" type="text" label="" clearable placeholder="" />
        <van-field v-model="phone" type="tel" label="" clearable placeholder="" />
        <!-- <van-cell is-link></van-cell> -->
        <van-field
          readonly
          clickable
          label=""
          :value="valueArea"
          placeholder=""
          @click="showPopup"
        />
        <van-field
          v-model="adrress"
          type="text"
          label=""
          clearable
          placeholder=""
        />
        <div class="but">
          <button class="butts bgkhui" @click="returnModify"></button>
          <button class="butts bgkblue" @click="postExhcangeData"></button>
        </div>
        <van-popup v-model="show1" position="bottom" close-on-click-overlay>
          <van-area
            :area-list="areaList"
            :columns-num="colNum"
            @cancel="cancelFn"
            @confirm="confirmFn"
            title=""
            value="450000"
          />
        </van-popup>
      </div>
    </van-overlay>
    <van-popup v-for="(item,index) in redList" :key="index" v-model="show4" :style="{ width: '100%' }" class="pop" :close-on-click-overlay='false'>
      <div class="red_envelopes" @touchmove.prevent>
        <img :src="redImg" />
        <div class="red_cont">
           <h3></h3>
           <p class="red_pack">{{''+item.name}}</p>
           <div class="red_but">
             <button class="back_home" @click="backHome"></button>
             <button class="get_now" @click="collarRedPackage(item.id)"></button>
           </div>
        </div>
      </div>
      <div class="icongunbi_but">
        <i class="iconfont icon-guanbi guanbi_but" @click="backHome"></i>
      </div>
    </van-popup>
    <van-overlay :show="show5">
      <div class="end-event">
        <h3></h3>
        <p></p>
        <div class="isState">
        <p class="text">
          2020103120201231
        </p>
        </div>
        <p class="bottom-text"></p>
        <p class="bottom-text">20201028</p>
        <div class="ok-but" @click="clickOk"></div>
      </div>
    </van-overlay>
  </div>
</template>

<script>
import AeraInfo from "../../utils/area";
import {
  IndexInfo,
  Exhcange, 
  GetExchangeRule,
  MyCard, 
  UpdateMyCard,
  HadpartIn,
  GetJssdkConfig,
  CheckByLocation,
  CheckPartIn,
  AcceptAward,
  CheckFirstTimeRedPacket } from "../../api/api";
import wx from 'weixin-js-sdk'
export default {
  inject: ['reload'], // 
  name: "IndexInfo",
  data() {
    return {
      clearable: false,
      show1: false, // 
      show2: false, // 
      show3: false, // 
      show4:false, // 
      show5:false,
      isState:0,
      brandName1:'',
      brandName2:'',
      colNum: 3, // 3-2-1-
      closeOnClickOverlay: false, // 
      areaList: AeraInfo, //
      valueArea: "", //
      arrArea: [], //
      activityId: [], // Id
      ayId:0,
      active: 0,
      stepValue: 0, // 
      shareValue: 0, // 
      userInfoData: "",
      activityInfo:[],
      prize:0, // 
      prohibitStep: true, // 
      prohibitShare: true, // 
      disabledinp: true,
      everyBarNeedBoxCount: 0, // 
      exchangeNumForBar: 0, //----
      exchangeNumForBox: 0, //----
      name: "", // 
      phone: "", // 
      adrress: "", // 
      url:'', // 
      expiredTime: '',
      remainingTime:false,
      timeExpired: 24*60*60, // 
      isDisable:true, // 
      otherHad:false, // 
      redImg:require('../../assets/img/redenvelopes.png'),
      redList:[],
      redId: '', // id
      redGiftType: '', // 01
      redName: '', // 
      redStatus: '', // -2-1 01
      setTimeoutName:null, // 
    };
  },
  mounted() {
      let that = this
      let date = new Date()
      let year = date.getFullYear(); 
      let month = date.getMonth()+1; 
      let day = date.getDate(); 
      let newDate = year+'-'+month+'-'+day
      let getDate = window.localStorage.getItem("setDate");
      if(getDate != '' && typeof(getDate) != undefined && getDate != null){
          if(getDate != newDate){
            that.show5 = true
            window.localStorage.setItem("setDate", newDate);
          }
      }else{
          that.show5 = true
          window.localStorage.setItem("setDate", newDate);
      }

      that.$getRequest(HadpartIn).then(res => {
        if (res.data.code === "0000") {
          let result = res.data.data.result
         that.activityId = result.activityIds
         let needLocationCheck = result.needLocationCheck
         if(needLocationCheck == '1'){
         var geolocation = new BMap.Geolocation();
          geolocation.getCurrentPosition(function(r){
          if(this.getStatus() == BMAP_STATUS_SUCCESS){
            that.$postRequest(CheckByLocation,{latitude:r.point.lat,longitude:r.point.lng,locationType:1}).then(res => {
                if(res.data.code=='0000') {
                  let had = res.data.data.result.had
                  if(had == false){
                    that.$postRequest(CheckPartIn).then(res => {
                      if(res.data.code === "0000"){
                        if(res.data.data.result.otherHad == true){
                          window.location.href = 'http://ld.thewm.cn/tdd_qw/static/page/index.html'
                        }else{
                          that.$dialog.alert({title: '', message: '',confirmButtonText:'',beforeClose(){wx.closeWindow()}});
                        }
                      }
                    })
                  }else{
                      that.postIndexInfoData();
                      that.getExchangeRuleData();
                      that.tankuan();
                      that.postMyCardData();
                      that.getCheckPartIn();
                      that.getCheckFirstTimeRedPacket();
                  }
                }else{
                 alert("")
                }
       })
          }else {
            alert('!');
          }        
        });
    }else{
      if(that.activityId.length == 0 || that.activityId == ''){
        that.$dialog.alert({title: '', message: '',confirmButtonText:'',beforeClose(){wx.closeWindow()}});
        return
      }
      that.postIndexInfoData();
      that.getExchangeRuleData();
      that.tankuan();
      that.postMyCardData();
      that.getCheckPartIn();
      that.getCheckFirstTimeRedPacket();
    }
        }
      });
    
     // input
    var hrt = document.documentElement.clientHeight;
      this.$nextTick(() => {
      document.getElementById('app').style.height= hrt+'px'
    })
  },
  methods: {
    // 
    getCheckFirstTimeRedPacket(){
      this.$postRequest(CheckFirstTimeRedPacket, {ids:this.activityId}).then(res => {
          if(res.data.code =='0000'){
            this.redList = res.data.data.list
            if(this.redList.length == '0'){
              this.show4 = false;
            }else{
              this.show4 = true;
            }
          }
      })
    },
   // 
   collarRedPackage(id){
    if(this.isDisable == false){
            return
        }
        this.isDisable = false
        this.$postRequest(AcceptAward,{giftId:id}).then(res => {
            this.isDisable = true
         if(res.data.code == '0000'){
          this.$toast('');
          window.location.href = res.data.data.gift.url
         }
     })
   },
   // 
     backHome(){
      this.show4 = false
     },
    // 
    onChangeStep: function(stepValue) {
      this.stepValue = stepValue;
    },
    // 
    onChangeShare: function(shareValue) {
      this.shareValue = shareValue;
    },
    // 
    stripPlus() {
      this.prize = this.prize - this.everyBarNeedBoxCount
      if(this.prize < this.everyBarNeedBoxCount){
       this.prohibitStep = true
      }else{
        this.prohibitStep = false
      }

      if(this.prize <= 0) {
        this.prohibitShare = true
      }else{
        this.prohibitShare = false
      }
      if(this.stepValue > 1){
        this.stepValue = 1
        this.$toast("");
        this.prohibitStep = true
        this.prize = this.prize + this.everyBarNeedBoxCount
      }

    },
    //
    stripMinus() {
      this.prize = this.prize + this.everyBarNeedBoxCount
      if(this.prize < this.everyBarNeedBoxCount){
       this.prohibitStep = true
      }else{
        this.prohibitStep = false
      }

      if(this.prize <= 0) {
        this.prohibitShare = true
      }else{
        this.prohibitShare = false
      }
    },
    // 
    sharePlus() {
      this.prize = this.prize - 1;
      if(this.prize <= 0) {
        this.prohibitShare = true
      }else{
        this.prohibitShare = false
      }
      if(this.prize < this.everyBarNeedBoxCount){
       this.prohibitStep = true
      }else{
        this.prohibitStep = false
      }
      if(this.shareValue > 4) {
        this.shareValue = 4
        this.$toast("4");
        this.prohibitShare = true
        this.prize = this.prize + 1;
      }
    },
    // 
    shareMinus() {
       this.prize = this.prize + 1;
       if(this.prize <= 0) {
        this.prohibitShare = true
      }else{
        this.prohibitShare = false
      }

      if(this.prize < this.everyBarNeedBoxCount){
       this.prohibitStep = true
      }else{
        this.prohibitStep = false
      }
    },
    // 
    guanbiBut() {
      this.show3 = false,
        this.stepValue = 0, // 
        this.shareValue = 0, // 
        this.name = "",
        this.phone = "",
        this.valueArea = "",
        this.adrress = "",
        this.postIndexInfoData();
    },
    
    // 
    postExhcangeData() {
      if (this.stepValue == 0 && this.shareValue == 0 || this.stepValue == '' && this.shareValue == '') {
        this.$toast("");
        return false;
      }
      if (this.name == "" || this.name == null) {
        this.$toast("");
        return false;
      }
      if (this.phone == "" || this.phone == null) {
        this.$toast("");
        return false;
      }
      if (this.valueArea == "" || this.valueArea == null) {
        this.$toast("");
        return false;
      }
      if (this.adrress == "" || this.adrress == null) {
        this.$toast("");
        return false;
      }
      if(this.isDisable == false){
            return
        }
        this.isDisable = false
      let params = {
        activityId: this.ayId,
        exchangeNumForBar: this.stepValue, // 
        exchangeNumForBox: this.shareValue, // 
        name: this.name,
        phone: this.phone,
        adrress: this.valueArea + this.adrress
      };
      this.$postRequest(Exhcange, params).then(res => {
        this.isDisable = true
        if (res.data.code === "0000") {
            this.show3 = false;
            this.postIndexInfoData()
            this.$router.push({path:'/ExchangeSuccess',query:{giftId:res.data.data.gift.id}})
            this.stepValue = 0, // 
            this.shareValue = 0, // 
            this.name = "",
            this.phone = "",
            this.valueArea = "",
            this.adrress = ""
        } else {
          this.$toast.fail(res.data.message);
        }
      });
    },
    //
    cancel: function() {
      this.show2 = false;
      this.stepValue = 0, // 
      this.shareValue = 0, // 
      this.postIndexInfoData();
    },
    //
    determine: function() {
      if ((this.stepValue == 0 || this.stepValue == '') && (this.shareValue ==0 || this.shareValue =='')) {
        this.show3 = false
        this.$toast('');
        } else {
        this.show3 = true;
        this.show2 = false
      }
    },
    // 
    returnModify: function() {
      this.show2 = true, 
      this.show3 = false;
    },
     
    // 
    postIndexInfoData: function() {
      this.$postRequest(IndexInfo, {ids:this.activityId}).then(
        res => {
          if (res.data.code === "0000") {
            let userInfo = res.data.data.userInfo;
            this.activityInfo = res.data.data.activityInfo;
            this.userInfoData = {
              id: userInfo.id,
              headImg: userInfo.headImg,
              nickName: userInfo.nickName,
              recommender: userInfo.recommender
            };
            
            for(let i =0;i<this.activityInfo.length;i++){
              let isComplete = this.activityInfo[i].isComplete
              let brandName = this.activityInfo[i].brandName
            if(isComplete == 1) {
              if(!window.sessionStorage.getItem('storge')){
                this.$dialog.alert({title: '', message: ''+brandName+'&nbsp'+''+'',confirmButtonText:''});
                window.sessionStorage.setItem('storge','true')
              }
            }else{
            }
            }
          }
        }
      );
    },
    // 
    getCheckPartIn(){
      let that = this
      this.$postRequest(CheckPartIn).then(res => {
         if(res.data.code === "0000"){
           that.otherHad = res.data.data.result.otherHad
         }
      })
    },
    qwPushmore(){
       window.location.href = 'http://ld.thewm.cn/tdd_qw/static/page/index.html'
    },
   // 
    getExchangeRuleData() {
      this.$getRequest(GetExchangeRule, { activityId: this.activityId }).then(
        res => {
          if (res.data.code === "0000") {
            this.everyBarNeedBoxCount = res.data.data.rule.everyBarNeedBoxCount;
          }
        }
      );
    },
    // 
    postMyCardData() {
         this.$postRequest(MyCard,{activityId:this.activityId}).then(res => {
           if (res.data.code == '0000') {
             this.url = res.data.data.code.url
             this.expiredTime = res.data.data.code.expiredTime
             if (this.expiredTime < this.timeExpired) {
               this.remainingTime = true
             } else {
                this.remainingTime = false
             }
           }
         })
       },
    // 
    postUpdateMyCard() {
       this.$postRequest(UpdateMyCard,{activityId:this.activityId}).then(res => {
        if (res.data.code == '0000') {
          this.$toast.success('')
        } else {
          this.$toast.fail(res.data.message)
        }
       })
    },
    clickOk(){
      let that = this;
      that.show5 = false
    },
    // 
    myReward() {
      this.$router.push({ path: "/MyReward" });
    },
    // 
    myTeam() {
      this.$router.push({
        path: "/MyTeam",
        query: { activityId: this.activityId}
      });
    },
    // 
    orderManagement() {
      this.$router.push({ path: "/OrderAdmin" });
    },
    // 
    myQrCode() {
      let that = this;
      if(this.expiredTime < this.timeExpired) {
        this.postUpdateMyCard()
        this.$router.push(
          {
            path: "/MyQrCode",query: { url: this.url}
          });
        return false
      } else {
        this.$router.push(
          {
            path: "/MyQrCode",query: { url: this.url}
          });
      }
    },
    // 
    tankuan() {
      this.show = false;
    },
    // 
    exchangeRules(prize,activityId) {
      this.prize = prize
      this.ayId = activityId
      if (this.prize < this.everyBarNeedBoxCount) {
              this.prohibitStep = true;
            } else {
              this.prohibitStep = false;
            }
      if (this.prize <= 0) {
        this.prohibitShare = true;
      } else {
        this.prohibitShare = false;
      }
      if(prize <= 0) {
        this.show2 = false
      } else {
        this.show2 = true;
      }
    },
    // 
    showPopup() {
      this.show1 = true;
    },
    // 
    cancelFn() {
      this.show1 = false;
    },
    // 
    confirmFn(event) {
      this.show1 = false;
      this.arrArea = event;
      let addrInfo = event[0].name + "-" + event[1].name + "-" + event[2].name;
      this.valueArea = addrInfo;
    }
  },
  beforeDestroy(){
    // 
    clearInterval(this.setTimeoutName)
  }
};
</script>

